import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    author_id_id:'',
    authorId:'',
    author_avatar: '',
    raw_text:'',
    author_gender:'',
    weibo_id:'',
    f:'',
    m:'',
    hot_comments:[],
    history_weibo:[],
    allData:{},
    screenData: {},
    weibo_word_cloud:{},
    comment_word_cloud:{},
    allNums:'',
    screenDataCQ:{},
    commentneg:'',
    commentother:'',
        //存储 token
        Authorization: localStorage.getItem('Authorization') ? localStorage.getItem('Authorization') : ''
  },
  mutations: {

      // 修改token，并将token存入localStorage
      changeLogin (state, user) {
        state.Authorization = user.Authorization;
        localStorage.setItem('Authorization', user.Authorization);
      },

      
    //存储大屏hot3,用于专题切换
    setHot3(state,author) {
      state.allData = author.allData
      localStorage.allData  = author.allData ;
    },
      //存储博主ID，用于详情页的渲染
      setAuthor (state, author) {
        state.author_id_id = author.author_id_id;
        localStorage.author_id_id  = author.author_id_id ;
      },
      //存储博主头像
      setAvatar (state, author) {
        state.author_avatar = author.author_avatar;
        localStorage.author_avatar  = author.author_avatar ;
      },
      //存储当前博文内容
      setText (state, author) {
        state.raw_text = author.raw_text;
        localStorage.raw_text  = author.raw_text ; 
      },
      //存储博主性别
      setGender (state, author) {
        state.author_gender = author.author_gender;
        localStorage.author_gender  = author.author_gender ; 

      },
      //存储博主昵称
      setNickName (state, author) {
        state.authorId = author.author_id;
        localStorage.authorId  = author.author_id ;
      },
      //存储博主所发布的历史博文
      setHistoryWeibo (state, author) {
        state.history_weibo = author.history_weibo;
        localStorage.history_weibo  = author.history_weibo;
       
      },
    //存储性别比例
      setGender (state, author) {
        state.f = author.f;
        state.m = author.m;
        localStorage.f = author.f;
        localStorage.m = author.m;
      },
      // 存储热点评论
      setCommit (state, author) {
        state.hot_comments = author.hot_comments;
        localStorage.hot_comments  = author.hot_comments;
      },
      //存储大屏数据
      setScreenData (state,screenData) {
        state.screenData = screenData;
        localStorage.screenData  = screenData;
      },
      setScreenDataCQ (state,screenDataCQ) {
        state.screenDataCQ = screenDataCQ;
        localStorage.screenDataCQ  = screenDataCQ;
      },

      //存储新闻词云数据
      setWeiboCloud(state,author) {
        state.weibo_word_cloud = author.weibo_word_cloud;
        localStorage.weibo_word_cloud = author.weibo_word_cloud
      },
      //存储评论词云
      setCommentCloud(state,author) {
        state.comment_word_cloud = author.comment_word_cloud;
        localStorage.comment_word_cloud = author.comment_word_cloud
      },
      //存储评论情感
      setCommentEmotion(state,author) {
        state.commentneg = author.neg;
        state.commentother = author.other;
        localStorage.commentneg = author.neg
        localStorage.commentother = author.other
      },
      setWebId(state,author){
        state.weibo_id = author.weibo_id;
        localStorage.weibo_id = author.weibo_id
      }
  },
  actions: {
  },
  modules: {
  }
})
