import Vue from 'vue'
import VueRouter from 'vue-router'


const Home = () => import('../views/Home.vue')
const Login = () => import('../views/Login.vue')
const Register = () => import('../views/Register.vue')
const Cqupt = () => import('../views/Cqupt.vue')

//一下是管理端的组页面
const Manage = () => import('../views/Manage.vue')

// 数据分析
const Manalyse = () => import('../components/content/manage/MAnalyse.vue')
// 舆情报告
const Mreport = () => import('../components/content/manage/analyse/Report.vue')



Vue.use(VueRouter)

const routes = [
  //页面组件
  {
    path: "/",
    redirect: "/login"
  },
  {
    path: '/login',
    component: Login

  },
  {
    path: '/register',
    component: Register
  },
  {
    path: '/home',
    name: 'Home',
    component: Home
  },
  {
    path:'/cqupt',
    name:'/Cqupt',
    component:Cqupt,
  },
  
  //管理端路由
  {
    path: '/manage',
    component: Manage,
    children: [ 
      {
        path: '/',
        component: Manalyse
      },
      {
        path: '/manage/report',
        component: Mreport
      },
    
    ]
  }

]

// router.beforeEach((to, from, next) => {
//   if (to.path === '/login') {
//     next();
//   } else {
//     let token = localStorage.getItem('Authorization');
 
//     if (token === 'null' || token === '') {
//       next('/login');
//     } else {
//       next();
//     }
//   }
// });

const router = new VueRouter({
  routes
})

export default router
